import React, { useState, useEffect, useRef } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { interviewService } from '../services/interviewService';

const InterviewSession = () => {
  const { interviewId } = useParams();
  const navigate = useNavigate();
  
  const [interview, setInterview] = useState(null);
  const [question, setQuestion] = useState(null);
  const [answer, setAnswer] = useState('');
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [questionNum, setQuestionNum] = useState(1);
  const [timeLeft, setTimeLeft] = useState(300);
  const [isRecording, setIsRecording] = useState(false);
  const [mediaRecorder, setMediaRecorder] = useState(null);
  const [audioChunks, setAudioChunks] = useState([]);
  const [showVideo, setShowVideo] = useState(false);
  const [stream, setStream] = useState(null);
  const videoRef = useRef(null);

  useEffect(() => {
    initializeInterview();
    return () => {
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }
    };
  }, [interviewId]);

  useEffect(() => {
    if (interview?.mode === 'text' && question) {
      const timer = setInterval(() => {
        setTimeLeft((prev) => {
          if (prev <= 1) {
            handleSubmitAnswer();
            return 300;
          }
          return prev - 1;
        });
      }, 1000);
      return () => clearInterval(timer);
    }
  }, [question, interview]);

  const initializeInterview = async () => {
    try {
      const response = await interviewService.getInterview(interviewId);
      setInterview(response.data);
      fetchNextQuestion();
    } catch (error) {
      console.error('Failed to load interview:', error);
      navigate('/dashboard');
    } finally {
      setLoading(false);
    }
  };

  const fetchNextQuestion = async () => {
    try {
      const response = await interviewService.getNextQuestion(interviewId);
      setQuestion(response.data);
      setTimeLeft(response.data.timeLimit || 300);
      setAnswer('');
      setAudioChunks([]);
    } catch (error) {
      console.error('Failed to fetch question:', error);
    }
  };

  const handleStartRecording = async () => {
    try {
      const audioStream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const recorder = new MediaRecorder(audioStream);
      
      recorder.ondataavailable = (e) => {
        if (e.data.size > 0) {
          setAudioChunks((prev) => [...prev, e.data]);
        }
      };

      recorder.start();
      setMediaRecorder(recorder);
      setIsRecording(true);
    } catch (error) {
      console.error('Failed to start recording:', error);
      alert('Failed to access microphone');
    }
  };

  const handleStopRecording = () => {
    if (mediaRecorder) {
      mediaRecorder.stop();
      mediaRecorder.stream.getTracks().forEach(track => track.stop());
      setIsRecording(false);
    }
  };

  const handleStartVideo = async () => {
    try {
      const videoStream = await navigator.mediaDevices.getUserMedia({ video: true });
      setStream(videoStream);
      if (videoRef.current) {
        videoRef.current.srcObject = videoStream;
      }
      setShowVideo(true);
    } catch (error) {
      console.error('Failed to start video:', error);
      alert('Failed to access camera');
    }
  };

  const handleStopVideo = () => {
    if (stream) {
      stream.getTracks().forEach(track => track.stop());
      setStream(null);
    }
    setShowVideo(false);
  };

  const handleSubmitAnswer = async () => {
    if (!answer.trim() && audioChunks.length === 0) {
      alert('Please provide an answer');
      return;
    }

    setSubmitting(true);

    try {
      let audioUrl = null;
      if (audioChunks.length > 0) {
        const audioBlob = new Blob(audioChunks, { type: 'audio/webm' });
        audioUrl = URL.createObjectURL(audioBlob);
      }

      const response = await interviewService.submitAnswer(
        interviewId,
        question.questionId,
        answer,
        audioUrl,
        null,
        300 - timeLeft
      );

      setQuestionNum((prev) => prev + 1);
      fetchNextQuestion();
    } catch (error) {
      console.error('Failed to submit answer:', error);
      alert('Failed to submit answer');
    } finally {
      setSubmitting(false);
    }
  };

  const handleComplete = async () => {
    try {
      const response = await interviewService.completeInterview(interviewId);
      navigate(`/interview/feedback/${interviewId}`);
    } catch (error) {
      console.error('Failed to complete interview:', error);
      alert('Failed to complete interview');
    }
  };

  if (loading) {
    return <div style={styles.loading}>Loading interview...</div>;
  }

  if (!question) {
    return <div style={styles.loading}>Loading question...</div>;
  }

  return (
    <div style={styles.container}>
      <div style={styles.header}>
        <div style={styles.progress}>
          <span style={styles.progressText}>Question {questionNum}</span>
          <div style={styles.progressBar}>
            <div style={{ ...styles.progressFill, width: `${(questionNum / 5) * 100}%` }} />
          </div>
        </div>
        {interview?.mode === 'text' && (
          <div style={{ ...styles.timer, color: timeLeft < 60 ? '#dc2626' : '#333' }}>
             {Math.floor(timeLeft / 60)}:{(timeLeft % 60).toString().padStart(2, '0')}
          </div>
        )}
      </div>

      <div style={styles.questionCard}>
        <div style={styles.questionCategory}>{question.category}</div>
        <h2 style={styles.questionText}>{question.question}</h2>
      </div>

      {interview?.mode === 'video' && showVideo && (
        <div style={styles.videoContainer}>
          <video ref={videoRef} autoPlay muted style={styles.video} />
          <button onClick={handleStopVideo} style={styles.stopVideoBtn}>Stop Camera</button>
        </div>
      )}

      <div style={styles.answerSection}>
        {interview?.mode === 'text' ? (
          <textarea
            value={answer}
            onChange={(e) => setAnswer(e.target.value)}
            placeholder="Type your answer here..."
            style={styles.textarea}
            rows={8}
          />
        ) : (
          <div style={styles.voiceControls}>
            {interview?.mode === 'video' && !showVideo && (
              <button onClick={handleStartVideo} style={styles.controlBtn}>
                 Start Camera
              </button>
            )}
            
            <button
              onClick={isRecording ? handleStopRecording : handleStartRecording}
              style={{
                ...styles.controlBtn,
                background: isRecording ? '#dc2626' : '#667eea',
                color: 'white'
              }}
            >
              {isRecording ? ' Stop Recording' : ' Start Recording'}
            </button>

            <textarea
              value={answer}
              onChange={(e) => setAnswer(e.target.value)}
              placeholder="Your transcribed answer will appear here, or type manually..."
              style={styles.textarea}
              rows={6}
            />
          </div>
        )}
      </div>

      <div style={styles.actions}>
        <button
          onClick={handleSubmitAnswer}
          disabled={submitting || (!answer.trim() && audioChunks.length === 0)}
          style={{
            ...styles.submitBtn,
            opacity: submitting || (!answer.trim() && audioChunks.length === 0) ? 0.5 : 1
          }}
        >
          {submitting ? 'Submitting...' : 'Submit Answer '}
        </button>
        
        {questionNum >= 5 && (
          <button onClick={handleComplete} style={styles.completeBtn}>
            Complete Interview
          </button>
        )}
      </div>

      <div style={styles.tips}>
        <h4> Tips for a great answer:</h4>
        <ul>
          <li>Use the STAR method (Situation, Task, Action, Result)</li>
          <li>Be specific with examples from your experience</li>
          <li>Keep your answer concise and focused</li>
          <li>Practice active listening and respond directly to the question</li>
        </ul>
      </div>
    </div>
  );
};

const styles = {
  container: {
    maxWidth: '800px',
    margin: '0 auto',
    padding: '2rem'
  },
  loading: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '50vh',
    fontSize: '1.2rem',
    color: '#666'
  },
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginBottom: '2rem'
  },
  progress: {
    flex: 1,
    marginRight: '2rem'
  },
  progressText: {
    fontSize: '0.9rem',
    color: '#666',
    marginBottom: '0.5rem',
    display: 'block'
  },
  progressBar: {
    height: '8px',
    background: '#e5e7eb',
    borderRadius: '4px',
    overflow: 'hidden'
  },
  progressFill: {
    height: '100%',
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    transition: 'width 0.3s'
  },
  timer: {
    fontSize: '1.5rem',
    fontWeight: '700'
  },
  questionCard: {
    background: 'white',
    padding: '2rem',
    borderRadius: '16px',
    boxShadow: '0 4px 20px rgba(0,0,0,0.08)',
    marginBottom: '2rem'
  },
  questionCategory: {
    display: 'inline-block',
    background: '#eef2ff',
    color: '#667eea',
    padding: '0.25rem 0.75rem',
    borderRadius: '20px',
    fontSize: '0.85rem',
    fontWeight: '500',
    marginBottom: '1rem'
  },
  questionText: {
    fontSize: '1.25rem',
    lineHeight: 1.6,
    color: '#333'
  },
  videoContainer: {
    marginBottom: '1rem',
    position: 'relative'
  },
  video: {
    width: '100%',
    maxHeight: '300px',
    objectFit: 'cover',
    borderRadius: '12px'
  },
  stopVideoBtn: {
    position: 'absolute',
    bottom: '1rem',
    right: '1rem',
    padding: '0.5rem 1rem',
    background: '#dc2626',
    color: 'white',
    border: 'none',
    borderRadius: '8px',
    cursor: 'pointer'
  },
  answerSection: {
    marginBottom: '2rem'
  },
  voiceControls: {
    display: 'flex',
    flexDirection: 'column',
    gap: '1rem'
  },
  controlBtn: {
    padding: '1rem 2rem',
    background: '#f9fafb',
    border: '2px solid #e5e7eb',
    borderRadius: '12px',
    fontSize: '1rem',
    fontWeight: '600',
    cursor: 'pointer',
    alignSelf: 'center'
  },
  textarea: {
    width: '100%',
    padding: '1rem',
    border: '2px solid #e5e7eb',
    borderRadius: '12px',
    fontSize: '1rem',
    fontFamily: 'inherit',
    resize: 'vertical'
  },
  actions: {
    display: 'flex',
    gap: '1rem',
    marginBottom: '2rem'
  },
  submitBtn: {
    flex: 1,
    padding: '1rem 2rem',
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    color: 'white',
    border: 'none',
    borderRadius: '12px',
    fontSize: '1rem',
    fontWeight: '600',
    cursor: 'pointer'
  },
  completeBtn: {
    padding: '1rem 2rem',
    background: '#10b981',
    color: 'white',
    border: 'none',
    borderRadius: '12px',
    fontSize: '1rem',
    fontWeight: '600',
    cursor: 'pointer'
  },
  tips: {
    background: '#f9fafb',
    padding: '1.5rem',
    borderRadius: '12px',
    borderLeft: '4px solid #667eea'
  }
};

export default InterviewSession;
